import { createRouter, createWebHashHistory } from 'vue-router';
import HomePage from '../components/HomePage.vue';


// 定义路由数组
const routes = [
  {
    path: '/',
    component: HomePage,
  },
  {
    path: '/ourfirm',
    component: () => import('../components/OurFirm.vue'), // 懒加载 OurFirm 组件
  },
  
  {
    path: '/esg',
    component: () => import('../components/ESG.vue'), 
  },
  {
    path: '/careers',
    component: () => import('../components/CyCareers.vue'), 
  },
  {
    path: '/contact',
    component: () => import('../components/CyContactPage.vue'), 
  },
  {
    path: '/diversity',
    component: () => import('../components/CyDiversity.vue'), 
  },
  {
    path: '/globalcredit',
    component: () => import('../components/GlobalCredit.vue'), 
  },
  {
    path: '/globalinsights',
    component: () => import('../components/GlobalInsights.vue'), 
  },
  {
    path: '/gis',
    component: () => import('../components/GlobalInvestmentSolutions.vue'), 
  },
  {
    path: '/gpe',
    component: () => import('../components/GlobalPrivateEquity.vue'), 
  },
  {
    path: '/news',
    component: () => import('../components/CyNews.vue'), 
  },
  {
    path: '/shareholders',
    component: () => import('../components/CyShareholders.vue'), 
  },
  {
    path: '/ar2022',
    component: () => import('../components/AR2022.vue'), 
  },
  {
    path: '/warn',
    component: () => import('../components/WarN.vue'), 
  },
];

// 创建并配置路由器
const router = createRouter({
  history: createWebHashHistory(),
  routes, // 使用路由数组
});

export default router;
